.form{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-content: center;
    
   

}
.h2tag{
    font-weight: bold;
}
.container{
    margin-top: 70px;
    background-color:white;
    box-sizing: border-box;
   border-radius: 15px;
    height: auto;
    width: 90vw;
   
    
}
.textfield{
    width: 80%;
    
    margin: auto;
    margin-top: 30px;
}
.textfieldin{
width: inherit;
}

@media screen and (min-width:900px){
    .textfield{
        width: 60%;
    
    } 

    .container{
        margin-top: 50px;
        background-color:white;
        box-sizing: border-box;
       border-radius: 15px;
        height: auto;
        width: 80vw;
    }    
}