@import url('https://fonts.googleapis.com/css2?family=Sacramento&display=swap');

.navbar{
    display:flex ;
    flex-direction:row;
    justify-content:space-between;
    width:90%;
    z-index: 5;
    
}
.img {
    width: 100%;
    height: inherit;
    object-fit: fill;
    
}
.textt{
    font-size: 3vw;
    color: rgb(255, 255, 255);
    font-family: 'Sacramento', cursive;
    height: auto;
    width: auto;
    z-index:100;
    font-weight: bold;
    letter-spacing:0.1em;
    box-sizing: border-box;
    text-align: center; 
   
  }


.textcontain{
    background-color:'yellow';
    position:'fixed';
   max-width:'300px';height:'600px';
    left:'50%';
    top:'30%';

} 
.navitem{
    display: none;
    position: relative;
    left: 70%;
    align-items: center;
    text-align: center;
    background-color: rgba(61,67,71,0);
    padding: auto;
}
.navmenu{
    display: inline
}

.textbox{
    background-color:rgba(0,0,0,0.5);
    position:absolute;
    width:40%;
    height:auto;
    left:30vw;
    z-index:50;
     top:20vh;
}

 @media screen and (min-width:900px){
     .img{
       width: auto;
       height: 90vh; 
       object-fit: cover;
    
    } 
    .navitem{
        display: flex;
        align-items: center;
    }
    .navmenu{
        display: none
    }
    .textcontain{
        background-color:'yellow';
        position:'fixed';
        width:'40%';height:'auto';
        left:'50%';
        top:'30%'
    }
    .textbox{
        background-color:rgba(0,0,0,0.3);
        border-radius: 15px;
        position:absolute;
        width:40%;
        height:auto;
        left:50vw;
        z-index:50;
         top:20vh;
    } 
}
 