@import url('https://fonts.googleapis.com/css2?family=Pacifico&family=Pangolin&family=Shadows+Into+Light&display=swap');
.img {
    height: inherit;
    width: inherit;
  object-fit: fill;
  z-index: 10;

}
.texthead{
    font-size: 2vw;
    color: white;
    font-family: 'Pangolin', cursive;
}
.text{
    font-size: 1.5vw;
    color: white;
    font-family: 'Pangolin', cursive;
}
.feturetext{
    background-color:rgba(135,30,31,1);
    width:auto;
    height:auto;
    z-index:50;
    padding:5px;
    border-radius:15px;
    top:5vh;
    max-width:30vw;
    max-height:70vh;
    position:relative;
    right:10%;
}
@media screen and (min-width:900px){
    .img {
        height: 90vh;
        width: auto;
        margin: 4%;
    
    }
    .feturetext{
        background-color:rgba(135,30,31,1);
        width:auto;
        height:auto;
        z-index:50;
        padding:5px;
        border-radius:15px;
        top:20vh;
        max-width:30vw;
        max-height:50vh;
        position:relative;
        right:15%;
    }

}
